import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {YourTripComponent} from './your-trip.component';
import {YourTripGuard} from './your-trip.guard';

/**
 * Adjust module routing here.
 */
const routes: Routes = [
    {
        path: '',
        component: YourTripComponent,
        canActivate: [YourTripGuard]
    }
];

@NgModule({
    imports: [
        RouterModule.forChild(routes),
    ],
    exports: [RouterModule],
})

export class YourTripRoutingModule {
}
